.header {
  padding: 20px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  background-color: transparent;

  &__container {
    @include flex-all-sb;
  }

  &__logo {
  }

  &__navbar {
    @media (max-width: 768px) {
      position: fixed;
      top: 0;
      left: -100%;
      overflow-y: auto;
      z-index: 100;
      width: 80%;
      max-width: 23.4375rem;
      height: 100vh;
      background-color: var(--light-color);
      transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
      box-shadow: 0.625rem 0 1.4375rem -0.3125rem rgba(34, 60, 80, 0.2);
    }
  }
}

.navbar {
  &__list {
    display: flex;
    gap: 40px;

    @media (max-width: 768px) {
      padding: 1.25rem;
      flex-direction: column;
    }
  }

  &__item {
  }

  &__link {
    font-size: 13px;
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 400;
    text-align: center;

    @media (max-width: 768px) {
      color: var(--black-color);
    }
  }
}

.menu--active {
  left: 0;
}
