@font-face {
  font-family: "Open Sans";
  src: url("../fonts/../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  padding: 20px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  background-color: transparent;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .header__navbar {
    position: fixed;
    top: 0;
    left: -100%;
    overflow-y: auto;
    z-index: 100;
    width: 80%;
    max-width: 23.4375rem;
    height: 100vh;
    background-color: var(--light-color);
    transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
    box-shadow: 0.625rem 0 1.4375rem -0.3125rem rgba(34, 60, 80, 0.2);
  }
}

.navbar__list {
  display: flex;
  gap: 40px;
}
@media (max-width: 768px) {
  .navbar__list {
    padding: 1.25rem;
    flex-direction: column;
  }
}
.navbar__link {
  font-size: 13px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 400;
  text-align: center;
}
@media (max-width: 768px) {
  .navbar__link {
    color: var(--black-color);
  }
}

.menu--active {
  left: 0;
}

.hero {
  position: relative;
  width: 100%;
  max-height: 450px;
  padding: 150px 0;
  text-align: center;
  background: url("../../img/banner-bg.jpg") center center/cover no-repeat;
}
.hero::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.55);
}
.hero__title {
  position: relative;
  z-index: 1;
  margin: 0 auto;
  max-width: 700px;
  font-size: clamp(1.625rem, 1.3415rem + 1.2258vw, 2.8125rem);
  line-height: 75px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
}

.section-offset {
  padding: 65px 0;
}

.burger {
  --burger-width: 30px;
  --burger-height: 30px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #fff;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 5px;
}
.burger::after {
  bottom: 5px;
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 769px) {
  .burger {
    display: none;
  }
}

.trends__title {
  position: relative;
  margin: 0;
  font-size: 20px;
  line-height: 29px;
  text-transform: uppercase;
  color: var(--black-color);
  font-weight: 700;
  text-align: center;
}
.trends__title::after {
  content: "";
  position: absolute;
  right: 50%;
  left: 50%;
  bottom: -10px;
  transform: translate(-50%, -50%);
  opacity: 0.4;
  width: 66px;
  height: 3px;
  background-color: var(--green-color);
}
.trends__wrapper {
  padding-top: 45px;
}
.trends__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(410px, 1fr));
  gap: 5px;
}
@media (max-width: 1655px) {
  .trends__list {
    grid-template-columns: repeat(2, minmax(410px, 1fr));
  }
}
@media (max-width: 840px) {
  .trends__list {
    grid-template-columns: repeat(1, 1fr);
  }
}
.trends__item {
  position: relative;
  min-height: 320px;
  display: flex;
  align-items: end;
}
.trends__item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.55);
}
.trends__item-first {
  background: url("../../img/trend1.jpg") center center/cover no-repeat;
}
.trends__item-second {
  background: url("../../img/trend2.jpg") center center/cover no-repeat;
}
.trends__item-third {
  background: url("../../img/trend3.jpg") center center/cover no-repeat;
}
.trends__item-fourth {
  background: url("../../img/trend4.jpg") center center/cover no-repeat;
}
.trends__article {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  margin: 0 25px 15px;
  padding: 40px 35px;
  border: 3px solid var(--green-color);
}

.article__list-category {
  display: flex;
  gap: 15px;
}
.article__link-category {
  font-size: 14px;
  text-transform: uppercase;
  color: var(--green-color);
  font-weight: 700;
}
@media (any-hover: hover) {
  .article__link-category:hover {
    text-decoration: underline;
  }
}
.article__title {
  margin: 0;
}
.article__title-link {
  font-size: 17px;
  text-transform: uppercase;
  color: var(--light-color);
  font-weight: 700;
  transition: 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .article__title-link:hover {
    color: var(--green-color);
  }
}

