.trends {
  &__container {
  }

  &__title {
    position: relative;
    margin: 0;

    font-size: 20px;
    line-height: 29px;
    text-transform: uppercase;
    color: var(--black-color);
    font-weight: 700;
    text-align: center;

    &::after {
      content: "";
      position: absolute;
      right: 50%;
      left: 50%;
      bottom: -10px;
      transform: translate(-50%, -50%);
      opacity: 0.4;
      width: 66px;
      height: 3px;
      background-color: var(--green-color);
    }
  }

  &__wrapper {
    padding-top: 45px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(410px, 1fr));
    gap: 5px;

    @media (max-width: 1655px) {
      grid-template-columns: repeat(2, minmax(410px, 1fr));
    }

    @media (max-width: 840px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    position: relative;
    min-height: 320px;

    display: flex;
    align-items: end;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.55);
    }
  }

  &__item-first {
    background: url("../../img/trend1.jpg") center center / cover no-repeat;
  }

  &__item-second {
    background: url("../../img/trend2.jpg") center center / cover no-repeat;
  }

  &__item-third {
    background: url("../../img/trend3.jpg") center center / cover no-repeat;
  }

  &__item-fourth {
    background: url("../../img/trend4.jpg") center center / cover no-repeat;
  }

  &__article {
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;

    margin: 0 25px 15px;
    padding: 40px 35px;
    border: 3px solid var(--green-color);
  }
}

.article {
  &__list {
  }

  &__list-category {
    display: flex;
    gap: 15px;
  }

  &__item {
  }

  &__item-category {
  }

  &__link {
  }

  &__link-category {
    font-size: 14px;
    text-transform: uppercase;
    color: var(--green-color);
    font-weight: 700;

    @include hover {
      text-decoration: underline;
    }
  }

  &__title {
    margin: 0;
  }

  &__title-link {
    font-size: 17px;
    text-transform: uppercase;
    color: var(--light-color);
    font-weight: 700;
    transition: 0.3s ease-in-out;

    @include hover {
      color: var(--green-color);
    }
  }
}
